html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  --blue-color: #00acdc;
  background: #fff;
  margin: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  box-shadow: 0 2px 4px #0000000d;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  display: flex;
}

.nav {
  height: 60px;
  margin-right: 50px;
  display: flex;
}

.img {
  height: 40px;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu:hover {
  color: var(--blue-color);
}

.nav-item {
  position: relative;
}

.nav-list .nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-list a {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  padding: 23px 0;
  line-height: normal;
  text-decoration: none;
  display: flex;
}

.is-active {
  position: relative;
  color: var(--blue-color) !important;
}

.is-active:after {
  content: "";
  background: var(--blue-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

